.citySection {
  position: relative;
  overflow: scroll;
  margin-right: -17px;
  min-height: 17px;
  max-height: 350px;
  margin-top: 20px;
}
.letterBlock {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-left: 40px;
  margin: 6px 0;
  &__letter {
    left: 0;
    position: absolute;
    font-weight: 500;
    font-size: 22px;
    color: #ff6900;
  }
}
